<template>
  <div class="mv">
    <div class="topNav">
      <img
        src="../assets/images/icon/returnKey_1.png"
        alt=""
        @click="$router.go(-1)"
      />
      <span>MV</span>
      <router-link
        :to="{
          path: '/Discuss/',
          query: { url: '/comment/mv?id=', id: dataMV.id },
        }"
        tag="i"
      ></router-link>
    </div>
    <div class="banner">
      <div class="content">
        <video autoplay controls :src="MVUrl"></video>
        <Loading v-if="isLoading"></Loading>
      </div>
    </div>
    <div class="bottom">
      <div class="left">
        <p>{{ dataMV.artistName }}</p>
        <p>{{ dataMV.name }}</p>
        <p>{{ dataMV.playCount }}次观看</p>
      </div>
      <div class="right">
        <button @click="peve">上一条</button>
        <button @click="next">下一条</button>
      </div>
    </div>
  </div>
</template>
<script>
import Loading from "@/components/Loading";
export default {
  data() {
    return {
      dataMV: {},
      MVUrl: "",
      isLoading: true,
      dataArr: [],
      index: 0,
    };
  },
  components: {
    Loading,
  },
  methods: {
    peve() {
      if (this.index != 0) {
        this.index--;
      } else {
        alert("到顶了 ");
      }
    },
    next() {
      if (this.index < this.dataArr.length - 1) {
        this.index++;
      } else {
        alert("没有更多了");
      }
    },
  },
  watch: {
    index: function(val) {
      this.isLoading = true;
      this.$axios
        .get("/mv/detail?mvid=" + this.dataArr[val].id)
        .then((data) => {
          return data.data.data;
        })
        .then((data) => {
          this.dataMV = data;
          this.MVUrl = data.brs["240"];
          this.isLoading = false;
        });
    },
  },
  beforeRouteEnter(to, from, next) {
    to, from;
    document.querySelector("audio").pause();  

    next();
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    // document.querySelector("audio").play();  
    next();
  },
  created() {
    this.dataArr = this.$route.query.data;
    this.index = this.$route.query.index;
    this.$axios
      .get("/mv/detail?mvid=" + this.$route.query.id)
      .then((data) => {
        return data.data.data;
      })
      .then((data) => {
        // console.log(data);
        this.dataMV = data;
        this.MVUrl = data.brs["240"];
        // this.$axios.get("/user/detail?uid=" + data.artists[0].id).then((user) => {
        //   console.log("dddd");
        //   console.log(user);
        // });
        this.isLoading = false;
      });
  },
};
</script>
<style lang="less" scoped>
.mv {
  width: 100%;
  height: 100vh;
  background-color: #010101;
  position: relative;
  z-index: 999;
  overflow: hidden;
  .topNav {
    padding: 3%;
    height: 4vh;
    border-bottom: 1px solid rgb(134, 134, 134);
    img {
      float: left;
    }
    span {
      display: inline-block;
      background-color: rgba(179, 179, 179, 0.431);
      padding: 1px 6px;
      border-radius: 20px;
      color: rgba(255, 255, 255, 0.534);
      margin-top: 5px;
      font-weight: 600;
    }
    i {
      width: 32px;
      height: 32px;
      float: right;
      background-image: url(../assets/images/icon/comment_white.png);
    }
  }
  .banner {
    width: 100%;
    height: 73vh;
    position: relative;
    .content {
      width: 100%;
      height: 40%;
      position: absolute;
      top: 50%;
      left: 0;
      //   background-color: salmon;
      transform: translateY(-50%);
      overflow: hidden;
      video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        border: 1px solid rgb(100, 100, 100);
      }
      .loading {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .bottom {
    width: 94%;
    height: 19vh;
    padding: 0 3%;
    .left {
      float: left;
      // background-color: blueviolet;
      p {
        text-align: left;
        // width: 50%;
        color: rgb(233, 233, 233);
        &:nth-child(1) {
          padding: 16px 0;
          font-weight: 600;
        }
        &:nth-child(2) {
          width: 200px;
          padding: 4px 0;
          font-size: 0.8rem;
          font-weight: 600;
          color: rgb(226, 226, 226);
        }
        &:nth-child(3) {
          font-size: 0.6rem;
          padding: 4px 0;
          color: rgb(138, 138, 138);
        }
      }
    }
    .right {
      float: right;
      button {
        width: 50px;
        height: 50px;
        display: block;
        border-radius: 8px;
        border: none;
        outline: none;
        margin-bottom: 5px;
        background-color: rgb(216, 216, 216);
      }
    }
  }
}
</style>
